import React from 'react';
import {
  List,
  ListItem,
  ListItemAvatar,
  Avatar,
  ListItemSecondaryAction,
  IconButton,
  Box,
  ListSubheader,
  Badge,
} from '@mui/material';
import { AddCircle, Delete, Edit } from '@mui/icons-material';
import { Placeholder } from '@/components/base/placeholder';
import { Character } from '@/schema';
import { EditableInput } from '@/components/app/input';
import { DialogFileUpload } from '@/components/app/file-upload';

export type CharacterEdit = Character;

export interface EditCastHandles {
  credits: CharacterEdit[];
}

export const EditCast = React.forwardRef<EditCastHandles, unknown>(
  (props, ref) => {
    const [credits, setCredits] = React.useState<CharacterEdit[]>([]);

    React.useImperativeHandle(ref, () => ({
      credits,
    }));

    const handleAddCredit = () => {
      setCredits([
        ...credits,
        {
          avatar: '',
          name: '',
          description: '',
          // tags: []
        },
      ]);
    };

    const handleChange = (
      e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>
    ) => {
      const { dataset, value } = e.target;
      if (dataset.idx && dataset.key) {
        const cur = credits[+dataset.idx];
        Object.assign(cur, {
          [dataset.key]: value,
        });
        setCredits([...credits]);
      }
    };

    const handleChangeAvatar = (idx: number, url: string) => {
      const cur = credits[idx];
      Object.assign(cur, { avatar: url });
      setCredits([...credits]);
    };

    const handleDelete = (idx: number) => {
      credits.splice(idx, 1);
      setCredits([...credits]);
    };

    return (
      <Box
        sx={(theme) => ({ backgroundColor: theme.palette.background.paper })}
      >
        <List
          subheader={
            <ListSubheader
              sx={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
              }}
            >
              演职表
              <IconButton onClick={handleAddCredit} size="small">
                <AddCircle fontSize="small" />
              </IconButton>
            </ListSubheader>
          }
        >
          {credits.length ? (
            <>
              {credits.map((item, idx) => {
                return (
                  <ListItem key={idx}>
                    <ListItemAvatar>
                      <DialogFileUpload
                        custom
                        onComplete={(url: string) =>
                          handleChangeAvatar(idx, url)
                        }
                      >
                        <Badge
                          overlap="circular"
                          anchorOrigin={{
                            vertical: 'top',
                            horizontal: 'right',
                          }}
                          badgeContent={<Edit fontSize="small" />}
                        >
                          <Avatar src={item.avatar}></Avatar>
                        </Badge>
                      </DialogFileUpload>
                    </ListItemAvatar>
                    <div>
                      <EditableInput
                        inputProps={{ 'data-idx': idx, 'data-key': 'name' }}
                        value={item.name}
                        onChange={handleChange}
                        placeholder="请输入名字"
                        maxRows={1}
                      />

                      <EditableInput
                        inputProps={{
                          'data-idx': idx,
                          'data-key': 'description',
                        }}
                        value={item.description}
                        onChange={handleChange}
                        placeholder="请输入描述"
                        maxRows={1}
                      />
                    </div>
                    <ListItemSecondaryAction>
                      <IconButton onClick={() => handleDelete(idx)}>
                        <Delete />
                      </IconButton>
                    </ListItemSecondaryAction>
                  </ListItem>
                );
              })}
            </>
          ) : (
            <Box justifyContent="center" display="flex">
              <Placeholder subtitle="新增演职员" />
            </Box>
          )}
        </List>
      </Box>
    );
  }
);

export default EditCast;
