import * as React from 'react';
import {
  Editor,
  EditorState,
  RichUtils,
  getDefaultKeyBinding,
  ContentBlock,
  DraftBlockType,
  DraftEditorCommand,
} from 'draft-js';
import { convertToHTML, convertFromHTML } from 'draft-convert';
import 'draft-js/dist/Draft.css';
import { BaseEditorProps, BaseEditorHandles } from './editor.interface';
import { Box } from '@mui/material';
import { Controls } from './controls';

export const NormalEditor = React.forwardRef<
  BaseEditorHandles,
  BaseEditorProps
>((props, ref) => {
  const { html, onBlur, placeholder } = props;
  const [editorState, setEditorState] = React.useState(() =>
    html
      ? EditorState.createWithContent(convertFromHTML(html))
      : EditorState.createEmpty()
  );

  React.useImperativeHandle(ref, () => ({
    getHtmlString: () => {
      return convertToHTML(editorState.getCurrentContent());
    },
  }));

  const handleKeyCommand = (
    cmd: DraftEditorCommand | string,
    state: EditorState
  ) => {
    const newState = RichUtils.handleKeyCommand(state, cmd);
    if (newState) {
      setEditorState(newState);
      return 'handled';
    }
    return 'not-handled';
  };

  const mapKeyToEditorCommand = (e: React.KeyboardEvent<{}>) => {
    // if (e.keyCode === 9 /* TAB */) {
    //   const newEditorState = RichUtils.onTab(e, editorState, 4 /* maxDepth */);
    //   if (newEditorState !== editorState) {
    //     setEditorState(newEditorState);
    //   }
    //   return null;
    // }
    return getDefaultKeyBinding(e);
  };

  const toggleBlockType = (blockType: DraftBlockType | string) => {
    setEditorState(RichUtils.toggleBlockType(editorState, blockType));
  };

  const toggleInlineStyle = (inlineStyle: string) => {
    setEditorState(RichUtils.toggleInlineStyle(editorState, inlineStyle));
  };

  // Custom overrides for "code" style.
  const styleMap = {
    CODE: {
      backgroundColor: 'rgba(0, 0, 0, 0.05)',
      fontFamily: '"Inconsolata", "Menlo", "Consolas", monospace',
      fontSize: 16,
      padding: 2,
    },
  };

  function getBlockStyle(block: ContentBlock) {
    switch (block.getType()) {
      case 'blockquote':
        return 'RichEditor-blockquote';
      default:
        return '';
    }
  }

  return (
    <Box>
      <Controls
        editorState={editorState}
        toggleBlockType={toggleBlockType}
        toggleInlineStyle={toggleInlineStyle}
      />
      <Box
        sx={(theme) => ({
          minHeight: 220,
          border: `1px solid ${theme.palette.divider}`,
        })}
      >
        <Editor
          blockStyleFn={getBlockStyle}
          customStyleMap={styleMap}
          editorState={editorState}
          handleKeyCommand={handleKeyCommand}
          keyBindingFn={mapKeyToEditorCommand}
          onChange={setEditorState}
          onBlur={onBlur}
          placeholder={placeholder}
          spellCheck={true}
        />
      </Box>
    </Box>
  );
});
