import React from 'react';
import {
  EditCast,
  EditMain,
  EditTop,
  EditCastHandles,
  EditTopHandles,
  EditMainHandles,
  EditAboutHandles,
  EditAbout,
} from './modules';
import {
  ContentScreen,
  AiderScreen,
  FullScreen,
  BodyScreen,
} from '@/layouts/page-layout';
import {
  Box,
  Tabs,
  Tab,
  SpeedDial,
  SpeedDialAction,
  SpeedDialIcon,
} from '@mui/material';
import { useCreateShadowMutation } from '@/schema';
import Confirm from '@/components/base/confirm';
import { Save } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';

export const ShadowUpload: React.FC = () => {
  const navigate = useNavigate();
  const [tab, setTab] = React.useState(0);
  const [open, setOpen] = React.useState(false);
  const topRef = React.createRef<EditTopHandles>();
  const mainRef = React.createRef<EditMainHandles>();
  const aboutRef = React.createRef<EditAboutHandles>();
  const castRef = React.createRef<EditCastHandles>();

  const [create_shadow] = useCreateShadowMutation({
    onCompleted() {
      Confirm.dialog({
        title: '提示',
        content: '上传成功。',
      }).then(() => {
        navigate('/home');
      });
    },
  });

  const handleCreateShadow = () => {
    const topFields = topRef.current;
    const mainFields = mainRef.current;
    const castFields = castRef.current;
    const aboutFields = aboutRef.current;

    create_shadow({
      variables: {
        shadow: {
          title: topFields?.title || '',
          sub_title: topFields?.sub_title || '',
          cover: topFields?.cover || '',
          posters: topFields?.posters,
          description: topFields?.description,
          credits: castFields?.credits,
          about: aboutFields?.about || '',
          sources: mainFields?.mediums.map((medium) => {
            const { name, sub_name, description, cover, ...rest } = medium;
            return {
              name: name || topFields?.title!,
              sub_name: sub_name || topFields?.sub_title,
              description: description || topFields?.description,
              cover: cover || topFields?.cover,
              ...rest,
            };
          }),
        },
      },
    });
  };

  return (
    <Box>
      <FullScreen>
        <BodyScreen>
          <EditTop ref={topRef} />
        </BodyScreen>
      </FullScreen>
      <BodyScreen>
        <ContentScreen
          sx={(theme) => ({
            backgroundColor: theme.palette.background.paper,
            padding: theme.spacing(1, 6),
            borderRight: `1px solid ${theme.palette.divider}`,
          })}
        >
          <Tabs value={tab} onChange={(e, val) => setTab(val)}>
            <Tab label="信息"></Tab>
            <Tab label="相关推荐" disabled></Tab>
            <Tab label="关于"></Tab>
          </Tabs>
          <div>
            <div hidden={tab !== 0}>
              <EditMain ref={mainRef} />
            </div>
            {/* {tab === 1 && <Relative />} */}
            <div hidden={tab !== 2}>
              <EditAbout ref={aboutRef} />
            </div>
            <SpeedDial
              ariaLabel="SpeedDial OpenIcon"
              sx={(theme) => ({
                position: 'fixed',
                '&.MuiSpeedDial-directionUp, &.MuiSpeedDial-directionLeft': {
                  bottom: theme.spacing(2),
                  right: theme.spacing(2),
                },
                '&.MuiSpeedDial-directionDown, &.MuiSpeedDial-directionRight': {
                  top: theme.spacing(2),
                  left: theme.spacing(2),
                },
              })}
              // hidden={hidden}
              // icon={<SpeedDialIcon openIcon={<EditIcon />} />}
              icon={
                <SpeedDialIcon
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                  }}
                />
              }
              onClose={() => setOpen(false)}
              onOpen={() => setOpen(true)}
              open={open}
            >
              <SpeedDialAction
                icon={<Save />}
                tooltipTitle="Create"
                onClick={handleCreateShadow}
              />
            </SpeedDial>
          </div>
        </ContentScreen>
        <AiderScreen>
          <EditCast ref={castRef} />
        </AiderScreen>
      </BodyScreen>
    </Box>
  );
};
export default ShadowUpload;
