import * as React from 'react';
import { styled } from '@mui/material/styles';
import { ThemeProvider, ToggleButtonGroup, ToggleButton } from '@mui/material';
import { YouTube } from '@mui/icons-material';
const PREFIX = 'withThird';

const classes = {
  wrap: `${PREFIX}-wrap`,
  tool: `${PREFIX}-tool`,
  grouped: `${PREFIX}-grouped`,
};

const Root = styled('div')(({ theme }) => ({
  [`&.${classes.wrap}`]: {
    position: 'relative',
    width: '100%',
  },

  [`& .${classes.tool}`]: {
    position: 'absolute',
    right: theme.spacing(1),
    top: '50%',
    transform: 'translateY(-50%)',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
    maxHeight: '50%',
    overflow: 'auto',
    background: 'rgba(0, 0, 0, .1)',
    backdropFilter: 'blur(8px)',
    color: '#fff',
    padding: theme.spacing(1),
    borderRadius: theme.shape.borderRadius,
    visibility: 'hidden',
    '$wrap:hover &': {
      visibility: 'initial',
    },
  },

  [`& .${classes.grouped}`]: {
    border: 'none',
    '&:not(:first-child)': {
      borderRadius: theme.shape.borderRadius,
    },
    '&:first-child': {
      borderRadius: theme.shape.borderRadius,
    },
  },
}));

export interface WithThirdProps {
  youtubeUrl?: string | null;
}

export function withThird<P>(Component: React.FC<P & WithThirdProps>) {
  return (props: P & WithThirdProps) => {
    const { youtubeUrl, ...rest } = props;
    const originUrl = rest && (rest as any).url ? (rest as any).url : undefined;
    const [bridgUrl, setBridgUrl] = React.useState<string>(originUrl);
    const [source, setSource] = React.useState<string>();

    const showThird =
      youtubeUrl &&
      rest &&
      (rest as any).url &&
      (rest as any).url !== youtubeUrl;

    const handleChange = (
      event: React.MouseEvent<HTMLElement>,
      nextValue: string
    ) => {
      setSource(nextValue);
    };
    React.useEffect(() => {
      if (source === 'youtube') {
        setBridgUrl(youtubeUrl as string);
      } else {
        setBridgUrl(originUrl);
      }
    }, [source, originUrl, youtubeUrl]);

    return (
      <Root className={classes.wrap}>
        <Component {...(rest as P)} url={bridgUrl} />
        {showThird && (
          <div className={classes.tool}>
            <ThemeProvider theme={{ palette: { type: 'dark' } }}>
              <ToggleButtonGroup
                classes={{ grouped: classes.grouped }}
                orientation="vertical"
                value={source}
                exclusive
                onChange={handleChange}
              >
                <ToggleButton value="youtube">
                  <YouTube></YouTube>
                </ToggleButton>
              </ToggleButtonGroup>
            </ThemeProvider>
          </div>
        )}
      </Root>
    );
  };
}
export default withThird;
