import * as React from 'react';
import { useTheme } from '@mui/material/styles';
import { Dialog, Toolbar, AppBar, Button, Box, Fab } from '@mui/material';
import {
  FullScreen,
  BodyScreen,
  ContentScreen,
  AiderScreen,
} from '@/layouts/page-layout';
import { Image } from '@/components/base/image';
import { DEFULAT_SHADOW_COVER } from '@/common/constants/default.constant';
import { Placeholder } from '@/components/base/placeholder';
import { DialogFileUpload } from '@/components/app/file-upload';
import ImageIcon from '@mui/icons-material/Image';
import { MovieCreation } from '@mui/icons-material';
import { ShadowPlayer } from '@/components/app/player';
import { EditInfo, EditInfoHandles } from './edit-info';

export interface EditMediumInfoProp {
  open: boolean;

  onSave?(medium: unknown): void;

  onCancel?(): void;
}

export const EditMediumInfo: React.FC<EditMediumInfoProp> = (
  props: EditMediumInfoProp
) => {
  const { onSave, onCancel } = props;
  const theme = useTheme();

  const [open, setOpen] = React.useState(false);
  const [url, setUrl] = React.useState('');
  const [cover, setCover] = React.useState('');
  const infoRef = React.createRef<EditInfoHandles>();

  React.useEffect(() => {
    setOpen(props.open);
  }, [props.open]);

  const reset = () => {
    setOpen(false);
    setUrl('');
    setCover('');
  };

  const handleChangeCover = (imageUrl: string) => {
    if (imageUrl) {
      setCover(imageUrl);
    }
  };

  const handleChangeSource = (sourceUrl: string) => {
    if (sourceUrl) {
      setUrl(sourceUrl);
    }
  };

  const handleSave = () => {
    onSave &&
      onSave({
        url,
        cover,
        name: infoRef.current?.name,
        sub_name: infoRef.current?.sub_name,
        description: infoRef.current?.description,
      });
    handleCancle();
  };

  const handleCancle = () => {
    setOpen(false);
    onCancel && onCancel();
    reset();
  };

  return (
    <Box>
      <Dialog open={open} fullScreen onClose={handleCancle}>
        <AppBar position="fixed" sx={{ boxShadow: theme.shadows[0] }}>
          <Toolbar
            sx={{
              width: '100%',
              display: 'flex',
              justifyContent: 'space-between',
            }}
          >
            <Button onClick={handleSave} color="inherit">
              保存
            </Button>
            <Button onClick={handleCancle} color="inherit">
              取消
            </Button>
          </Toolbar>
        </AppBar>
        <Box sx={{ height: '100%' }}>
          <Toolbar />
          <FullScreen>
            <BodyScreen>
              <Box sx={{ width: '100%', position: 'relative' }}>
                {url ? (
                  <ShadowPlayer
                    url={url}
                    light={cover ? cover : DEFULAT_SHADOW_COVER}
                  />
                ) : (
                  <Image
                    aspectRatio={16 / 9}
                    src={cover ? cover : DEFULAT_SHADOW_COVER}
                  />
                )}
                <DialogFileUpload custom onComplete={handleChangeCover}>
                  <Fab
                    color={cover ? 'primary' : 'default'}
                    size="medium"
                    sx={{
                      position: 'absolute',
                      left: theme.spacing(4),
                      bottom: theme.spacing(4),
                    }}
                  >
                    <ImageIcon />
                  </Fab>
                </DialogFileUpload>
                <DialogFileUpload custom onComplete={handleChangeSource}>
                  <Fab
                    color={url ? 'primary' : 'default'}
                    size="medium"
                    sx={{
                      position: 'absolute',
                      right: theme.spacing(4),
                      bottom: theme.spacing(4),
                    }}
                  >
                    <MovieCreation />
                  </Fab>
                </DialogFileUpload>
              </Box>
            </BodyScreen>
          </FullScreen>
          <BodyScreen
            sx={{
              backgroundColor: theme.palette.background.paper,
              display: 'flex',
              margin: '0 auto',
            }}
          >
            <ContentScreen
              sx={{
                padding: theme.spacing(2, 5),
                borderRight: `1px solid ${theme.palette.divider}`,
              }}
            >
              <EditInfo ref={infoRef} />
            </ContentScreen>
            <AiderScreen
              sticky
              sx={{
                padding: theme.spacing(2, 4),
                height: '100%',
                overflow: 'auto',
              }}
            >
              <Placeholder subtitle="评论区" sx={{ width: '100%' }} />
            </AiderScreen>
          </BodyScreen>
        </Box>
      </Dialog>
    </Box>
  );
};
