import * as React from 'react';
import { styled } from '@mui/material/styles';
import { Image } from '@/components/base/image';
import { DEFULAT_SHADOW_COVER } from '@/common/constants/default.constant';
import {
  Box,
  ImageList,
  ImageListItem,
  IconButton,
  Switch,
  Tooltip,
  Zoom,
} from '@mui/material';

import { useEditableInput, EditableInput } from '@/components/app/input';
import { DialogFileUpload } from '@/components/app/file-upload';
import { PlayCircleOutline, Delete } from '@mui/icons-material';
import SwipeableViews from 'react-swipeable-views';
import { autoPlay } from 'react-swipeable-views-utils';
import { AspectRatioBox } from '@/components/base/aspect-ration-box';

const PREFIX = 'EditTop';

const classes = {
  root: `${PREFIX}-root`,
  aider: `${PREFIX}-aider`,
  aiderContent: `${PREFIX}-aiderContent`,
  main: `${PREFIX}-main`,
  mainContent: `${PREFIX}-mainContent`,
  poster: `${PREFIX}-poster`,
  posterTool: `${PREFIX}-posterTool`,
};

const Root = styled('div')(({ theme }) => ({
  [`&.${classes.root}`]: {
    position: 'relative',
    width: '100%',
  },

  [`& .${classes.aider}`]: {
    position: 'absolute',
    left: 0,
    top: 0,
    bottom: 0,
    width: '45%',
    padding: theme.spacing(8),
  },

  [`& .${classes.aiderContent}`]: {
    width: '50%',
    padding: theme.spacing(1),
    borderRadius: theme.shape.borderRadius,
    maxHeight: '100%',
    background: 'rgba(0, 0, 0, .1)',
    backdropFilter: 'blur(8px)',
    overflow: 'auto',
  },

  [`& .${classes.main}`]: {
    position: 'absolute',
    top: '50%',
    right: 0,
    width: '50%',
    padding: theme.spacing(0, 8),
    transform: 'translate(0, -50%)',
    color: '#fff',
    display: 'flex',
    flexDirection: 'column',
  },

  [`& .${classes.mainContent}`]: {
    background: 'rgba(0, 0, 0, .1)',
    backdropFilter: 'blur(8px)',
    padding: theme.spacing(4),
    borderRadius: theme.shape.borderRadius,
  },

  [`& .${classes.poster}`]: {
    position: 'relative',
  },

  [`& .${classes.posterTool}`]: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    visibility: 'hidden',
    position: 'absolute',
    padding: theme.spacing(1),
    top: 0,
    left: 0,
    width: '100%',
    color: '#fff',
    '$poster:hover &': {
      visibility: 'initial',
    },
  },
}));

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

export interface EditTopHandles {
  title: string;

  sub_title?: string;

  description?: string;

  posters?: string[];

  cover: string;
}

export const EditTop = React.forwardRef<EditTopHandles, unknown>(
  (props, ref) => {
    const [activeStep, setActiveStep] = React.useState(0);
    const [title, setTitle] = useEditableInput('');
    const [sub_title, setSubTitle] = useEditableInput('');
    const [description, setDescription] = useEditableInput('');
    const [posters, setPosters] = React.useState<string[]>([]);
    const [cover, setCover] = React.useState<string>('');

    React.useEffect(() => {
      if (posters && posters.length > 0) {
        if (!cover || !posters.includes(cover)) setCover(posters[0]);
      } else {
        setCover('');
      }
    }, [cover, posters]);

    React.useImperativeHandle(ref, () => ({
      title,
      sub_title,
      description,
      posters,
      cover,
    }));

    const handleAddPoster = (imageUrl: string) => {
      if (imageUrl && !posters.includes(imageUrl)) {
        setPosters([...posters, imageUrl]);
      }
    };

    const handleRemovePoster = (
      e: React.MouseEvent<HTMLDivElement, MouseEvent>
    ) => {
      const result = window.confirm('delete?');
      if (result) {
        const { dataset } = e.currentTarget;
        if (dataset.idx) {
          posters.splice(+dataset.idx, 1);
          setPosters([...posters]);
        }
      }
    };

    const handleChangeCover = (idx: number) => {
      setCover(posters[idx]);
    };

    return (
      <Root className={classes.root}>
        {posters.length ? (
          <AutoPlaySwipeableViews
            onChangeIndex={(step: number) => setActiveStep(step)}
            index={activeStep}
          >
            {posters.map((image, idx) => {
              return <Image key={idx} aspectRatio={16 / 9} src={image} />;
            })}
          </AutoPlaySwipeableViews>
        ) : (
          <Image aspectRatio={16 / 9} src={DEFULAT_SHADOW_COVER} />
        )}

        <div className={classes.aider}>
          <div className={classes.aiderContent}>
            <ImageList rowHeight="auto" cols={1}>
              {posters.map((poster, idx) => (
                <ImageListItem key={poster} cols={1} className={classes.poster}>
                  <Image
                    aspectRatio={16 / 9}
                    src={poster}
                    alt={'poster' + idx}
                  />
                  <div className={classes.posterTool}>
                    <div>
                      <Tooltip
                        TransitionComponent={Zoom}
                        title="设成封面"
                        placement="bottom"
                        arrow
                      >
                        <Switch
                          size="small"
                          color="primary"
                          checked={cover === poster}
                          onChange={() => handleChangeCover(idx)}
                        />
                      </Tooltip>
                    </div>
                    <div data-idx={idx} onClick={handleRemovePoster}>
                      <Tooltip
                        TransitionComponent={Zoom}
                        title="删除"
                        placement="bottom"
                        arrow
                      >
                        <IconButton size="medium">
                          <Delete fontSize="small" />
                        </IconButton>
                      </Tooltip>
                    </div>
                  </div>
                </ImageListItem>
              ))}
              <ImageListItem cols={1}>
                <AspectRatioBox ratio={16 / 9}>
                  <Box
                    display="flex"
                    alignItems="center"
                    justifyContent="center"
                    height="100%"
                  >
                    <DialogFileUpload onComplete={handleAddPoster} />
                  </Box>
                </AspectRatioBox>
              </ImageListItem>
            </ImageList>
          </div>
        </div>
        <div className={classes.main}>
          <div className={classes.mainContent}>
            <EditableInput
              variant="h4"
              value={title}
              onChange={setTitle}
              placeholder="请输入标题"
            />
            <EditableInput
              variant="subtitle1"
              value={sub_title}
              onChange={setSubTitle}
              placeholder="请输入副标题"
            />
            <EditableInput
              variant="body2"
              value={description}
              onChange={setDescription}
              placeholder="请输入描述..."
            />
            <Box mt={3}>
              <IconButton color="inherit" disabled>
                <PlayCircleOutline fontSize="large" />
              </IconButton>
            </Box>
          </div>
        </div>
      </Root>
    );
  }
);

export default EditTop;
