import React from 'react';
import {
  Box,
  // Typography,
  ImageList,
  ImageListItem,
  Button,
} from '@mui/material';
import { MediaOwn, withTool } from '@/components/app/media';
import { EditMediumInfo } from '../edit-medium-info';
import { Placeholder } from '@/components/base/placeholder';
import { CreateShadowMediumInput } from '@/schema';

const EditMediaOwn = withTool(MediaOwn);

export interface EditMainHandles {
  mediums: CreateShadowMediumInput[];
}

export const EditMain = React.forwardRef<EditMainHandles, unknown>(
  (props, ref) => {
    const [openMedium, setOpenMedium] = React.useState(false);
    const [mediums, setMediums] = React.useState<CreateShadowMediumInput[]>([]);

    React.useImperativeHandle(ref, () => ({
      mediums: mediums,
    }));

    const handleSave = (medium: CreateShadowMediumInput) => {
      setMediums([...mediums, medium]);
    };

    const handleDelete = (medium: unknown) => {
      const idx = mediums.findIndex((item) => item === medium);
      if (idx !== -1) {
        mediums.splice(idx, 1);
        setMediums([...mediums]);
      }
    };

    return (
      <Box>
        <Box sx={(theme) => ({ padding: theme.spacing(1, 0) })}>
          <Box sx={(theme) => ({ padding: theme.spacing(2, 0) })}>
            <ImageList rowHeight="auto" cols={4}>
              {mediums.map((item, idx) => {
                return (
                  <ImageListItem cols={1} key={idx}>
                    <EditMediaOwn
                      {...item}
                      onDelete={() => handleDelete(item)}
                    />
                  </ImageListItem>
                );
              })}

              <Button onClick={() => setOpenMedium(true)}>
                <Placeholder subtitle="新增媒体资源" />
              </Button>
            </ImageList>
          </Box>
        </Box>
        <EditMediumInfo
          open={openMedium}
          onSave={handleSave}
          onCancel={() => setOpenMedium(false)}
        />
      </Box>
    );
  }
);

export default EditMain;
