import * as React from 'react';
import { styled } from '@mui/material/styles';
import ReactPlayer, { ReactPlayerProps } from 'react-player';
import { AspectRatioBox } from '@/components/base/aspect-ration-box';

const PREFIX = 'ShadowPlayer';

const classes = {
  player: `${PREFIX}-player`,
};

const StyledAspectRatioBox = styled(AspectRatioBox)(() => ({
  [`& .${classes.player}`]: {
    width: '100% !important',
    height: '100% !important',
    background: '#000',
  },
}));

export interface PlayerBaseProps
  extends Omit<
    ReactPlayerProps,
    'url' | 'playing' | 'controls' | 'muted' | 'wrapper' | 'light'
  > {}

export const ShadowPlayer: React.FC<PlayerBaseProps> = (props) => {
  const { url, ...rest } = props;
  const isM3u8 = url?.toString().endsWith('.m3u8');
  const videoRef = React.createRef<ReactPlayer>();

  return (
    <StyledAspectRatioBox ratio={16 / 9}>
      <ReactPlayer
        ref={videoRef}
        className={classes.player}
        {...rest}
        url={url}
        config={{
          file: {
            forceHLS: isM3u8,
            attributes: {
              controlsList: 'nodownload noremoteplayback',
              disablePictureInPicture: true,
              // onContextMenu: (e) => e.preventDefault(),
            },
          },
          youtube: {
            playerVars: {
              autoplay: rest.playing,
              modestbranding: 1,
              rel: 0,
              showinfo: 0,
              controls: rest.controls,
            },
            embedOptions: {},
          },
        }}
      />
    </StyledAspectRatioBox>
  );
};

ShadowPlayer.defaultProps = {
  controls: true,
  playing: true,
};

export default ShadowPlayer;
