import * as React from 'react';
import { NormalEditor } from '@/components/base/editor';
import { EditorHandles } from '@/components/base/editor/editor.interface';

export interface EditAboutHandles {
  about?: string;
}

export const EditAbout = React.forwardRef<EditAboutHandles, unknown>(
  (props, ref) => {
    const [aboutHtml, setAboutHtml] = React.useState('');

    React.useImperativeHandle(ref, () => ({
      about: aboutHtml,
    }));
    const editorRef = React.createRef<EditorHandles>();

    const handleBlur = () => {
      const htmlString = editorRef.current?.getHtmlString();
      setAboutHtml(htmlString);
    };

    return (
      <div>
        <NormalEditor ref={editorRef} onBlur={handleBlur} />
      </div>
    );
  }
);

export default EditAbout;
