import * as React from 'react';
import { styled } from '@mui/material/styles';
import { Typography, Box, IconButton, Avatar } from '@mui/material';
import { ThumbUpAlt, ThumbDownAlt, MoreVert } from '@mui/icons-material';
import moment from 'moment';
import { EditableInput, useEditableInput } from '@/components/app/input';
import { useAuth } from '@/hooks';

const PREFIX = 'EditInfo';

const classes = {
  root: `${PREFIX}-root`,
  header: `${PREFIX}-header`,
  infoItem: `${PREFIX}-infoItem`,
  toolBox: `${PREFIX}-toolBox`,
  toolText: `${PREFIX}-toolText`,
  description: `${PREFIX}-description`,
};

const Root = styled('div')(({ theme }) => ({
  [`& .${classes.root}`]: {},

  [`& .${classes.header}`]: {
    display: 'flex',
    justifyContent: 'space-between',
    marginBottom: theme.spacing(1),
  },

  [`& .${classes.infoItem}`]: {
    '& + &': {
      marginLeft: theme.spacing(1),
    },
  },

  [`& .${classes.toolBox}`]: {
    display: 'flex',
    alignItems: 'center',
    marginRight: theme.spacing(2),
  },

  [`& .${classes.toolText}`]: {
    marginLeft: theme.spacing(0.5),
  },

  [`& .${classes.description}`]: {
    marginBottom: theme.spacing(1),
  },
}));

export interface EditInfoHandles {
  name?: string;

  sub_name?: string;

  description?: string;
}

export const EditInfo = React.forwardRef<EditInfoHandles, unknown>(
  (props, ref) => {
    const [name, setName] = useEditableInput('');
    const [sub_name, setSubName] = useEditableInput('');
    const [description, setDescription] = useEditableInput('');
    const { member } = useAuth();

    React.useImperativeHandle(ref, () => ({
      name,
      sub_name,
      description,
    }));

    return (
      <Root>
        <Box className={classes.header}>
          <div>
            <Typography variant="subtitle1">
              <EditableInput
                value={name}
                onChange={setName}
                placeholder="请输入标题"
              />
              <EditableInput
                value={sub_name}
                onChange={setSubName}
                placeholder="请输入副标题"
              />
            </Typography>
            <Box display="flex">
              <Typography variant="caption" className={classes.infoItem}>
                发布时间: {moment(Date.now()).format('YYYY-MM-DD HH:mm')}
              </Typography>
            </Box>
          </div>
          <Box display="flex" alignItems="center">
            <Box className={classes.toolBox}>
              <IconButton size="small">
                <ThumbDownAlt fontSize="small" />
              </IconButton>
              <Typography className={classes.toolText}>--</Typography>
            </Box>
            <Box className={classes.toolBox}>
              <IconButton size="small">
                <ThumbUpAlt fontSize="small" />
              </IconButton>
              <Typography className={classes.toolText}>--</Typography>
            </Box>
            <IconButton size="small">
              <MoreVert fontSize="small" />
            </IconButton>
          </Box>
        </Box>
        <Typography className={classes.description} variant="body2">
          <EditableInput
            variant="body2"
            value={description}
            onChange={setDescription}
            placeholder="请输入描述..."
          />
        </Typography>

        <Box display="flex" justifyContent="space-between" alignItems="center">
          <Box display="flex" alignItems="center">
            <Avatar src={member?.avatar}></Avatar>
            <Box ml={1}>
              <Typography variant="subtitle2">{member?.nickname}</Typography>
              <Typography variant="caption">{member?.description}</Typography>
            </Box>
          </Box>
        </Box>
      </Root>
    );
  }
);

export default EditInfo;
