import { useGetCosCredentialLazyQuery } from '@/schema';
import moment from 'moment';
import COS from 'cos-js-sdk-v5';
import { COS_BUCKET, COS_REGION } from '@/common/constants/cos.constant';

const useUploadHelper = () => {
  const [getCosCredential] = useGetCosCredentialLazyQuery({
    onError(error) {
      console.error(error);
    },
  });
  const cos = new COS({
    async getAuthorization(_, callback) {
      getCosCredential().then(({ data }) => {
        const result = data?.get_cos_credential;
        callback({
          TmpSecretId: result!.credentials.tmpSecretId,
          TmpSecretKey: result!.credentials.tmpSecretKey,
          SecurityToken: result!.credentials.sessionToken,
          StartTime: result!.startTime,
          ExpiredTime: result!.expiredTime,
        });
      });
    },
  });

  const uploadFile = async (file: File) => {
    const now = Date.now();
    const dateString = moment(now).format('YYYY-MM-DD');
    const result = await cos.uploadFile({
      Bucket: COS_BUCKET,
      Region: COS_REGION,
      Key: `/upload/${dateString}/${now}`,
      Body: file,
    });
    console.log(result);
    return `${result.Location.replace(
      `${COS_BUCKET}.cos.${COS_REGION}.myqcloud.com`,
      process.env.REACT_APP_HTTP_DOMAIN_URL!
    )}`;
  };

  return {
    uploadFile,
  };
};

export default useUploadHelper;
