import React, { useState } from 'react';
import { BaseFileUploadProps } from './file-upload.interface';
import { Box, Typography } from '@mui/material';
import { CloudUpload } from '@mui/icons-material';
import { useSnackbar } from 'notistack';
import useUploadHelper from '@/hooks/upload.helper';

export const FileUpload: React.FC<BaseFileUploadProps> = (props) => {
  const { onComplete, onError, children, component, custom, title, maxSize } =
    props;
  const { enqueueSnackbar } = useSnackbar();
  const { uploadFile } = useUploadHelper();
  const [loading, setLoading] = useState(false);

  const inputRef = React.createRef<HTMLInputElement>();

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { validity, files } = e.currentTarget;
    const file = files?.[0];
    if (file && maxSize && file.size > maxSize * 1024 * 1024) {
      enqueueSnackbar('超出尺寸限制', { variant: 'warning' });
      return;
    }
    if (validity.valid) {
      setLoading(true);
      uploadFile(file!)
        .then((url) => {
          onComplete && onComplete(url);
        })
        .catch((error) => {
          onError && onError(error.message);
        })
        .finally(() => {
          setLoading(false);
        });
    }
  };

  const handleClick = () => {
    if (!loading) {
      inputRef.current?.click();
    }
  };

  const Tag = component || 'div';

  return (
    <Tag role="button" onClick={handleClick}>
      <input
        ref={inputRef}
        style={{ display: 'none' }}
        type="file"
        accept="*"
        onChange={handleChange}
      />
      {custom ? (
        <>{children}</>
      ) : (
        <Box
          sx={(theme) => ({
            display: 'inline-flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            width: props.width ?? '100%',
            height: props.height ?? '100%',
            color: theme.palette.text.secondary,
            flex: 1,
          })}
        >
          <div>
            <CloudUpload />
          </div>
          <Typography variant="h6">{loading ? '上传中...' : title}</Typography>
        </Box>
      )}
    </Tag>
  );
};

FileUpload.defaultProps = {
  custom: false,
  title: '点击上传',
  maxSize: 200,
};
